*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
html{
    /* 드래그 방지 */
    -ms-user-select: none;
    -moz-user-select: -moz-none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    user-select: none;
}
ul{
    list-style: none;
}
a{
    text-decoration: none;
}